import React, {useRef, useEffect} from 'react';
import {
    ImageWrapper,
    Video,
    VideoWrapper

} from "./style";
import {useMediaQuery} from "react-responsive";
import image from "../../assets/background.png"

// margin-left: ${({ isMobile }) => (isMobile ? '0px' : '16px')};

const AutoreverseVideoBackground= ({src}) => {
    const isMobileWidth = useMediaQuery({maxWidth: 768})
    const videoRef = useRef(null);
    useEffect(() => {
        const video = videoRef.current;

        const handleVideoEnd = () => {
            video.currentTime = 0;
            video.playbackRate = -1;
            video.play();
        };

        video.addEventListener('ended', handleVideoEnd);

        return () => {
            video.removeEventListener('ended', handleVideoEnd);
        };
    }, []);



        return (
            <div>
                <VideoWrapper alignBottom={true}>
                    <Video ref={videoRef} src={src} loop autoPlay muted playsInline/>
                </VideoWrapper>
            </div>

        );


};

export default AutoreverseVideoBackground;
